	module.exports = {"data":{"markdownRemark":{"html":"<p>帰宅中に東上線区間でたまたま遭遇した東京メトロ10000系。折角なので乗ってみるテスト。今まで外から見たことなら何度かあったけどね。んでちょいと感想。鉄な用語がちょこちょこ出るかもだけどぐぐってくださいな。 外観は緩行電車にしては珍しい流線型の顔を持っている。外から見てまず思ったのが「なにこの特急車両」だったし。これでもしLEDヘッドマークが先頭の避難扉下部に埋め込まれていたらもう完璧。そして赤茶帯。最終的には13号線(有楽町新線)専用になるのかな？ 外見はモロ特急形だったのに入ってみると第一印象は「なにこのE231系」だった。山手線みたいなデュアルモニタLCDが埋め込まれてるし。(しかも画面情報のデザインまでそっくり！)そしてドアチャイムまでも全く同じ音なのには流石に違和感覚えた。なんか東武線にJR車両が走ってるみたいで。しかも東武は発車メロディがない(警笛だけ)のでなおさら違和感。(最初はメトロ07系みたいな2回チャイムだと思ってたんだが) 内装は一見E231系と東武50000系を足して2で割った感じだがよーくみると両者よりもかなり金かけてるな、ってのがよく理解出来る。E231系ではプラスチックで出来てるところがフルメタルだったり、貫通扉が全面ガラスだったり、天井が間接照明だったりとかなり素敵。欲を言えば間接照明がオレンジ蛍光灯で全席クロスシートの中間車両が欲しかったなぁ。(どうしても特急形にしたいらしい) 駆動音はまた聞いたことがないVVVF音が。比較的静かなほう。でも東武50000系の静音性には劣る。折角だからシーメンス入れてよ（ｒｙ 最近の車両はやたら安かろう悪かろうが多かったのでこの車両は結構高級感あって好きかも。ってか川越市までに2度こいつにすれ違ったんだが。一体何編成入ったんだ？下手すると東武50000系よりたくさん入ってたりして。伊勢崎線の30000系と東上線の8000系交換しませんか？＞T武鉄道さん いやむしろ100系よこせ東上線にも全席指定通勤ライナー走らせろ 'Ａ｀</p>","frontmatter":{"cover":null,"date":"2006-09-14T23:42:00+09:00","strDate":"2006/9/14","title":"東京メトロ10000系に乗る"}}},"pathContext":{"path":"/ja/blog/2006/09/14-東京メトロ10000系に乗る/","langKey":"ja"}}

